.chat {
  padding: 10rem 2rem 2rem 2rem;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.container-chat {
  width: 80%;
  height: 80vh;
  display: flex;
}
.sidebar {
  flex: 1;
  display: flex;
  flex-direction: column;
}
.sidebar-top {
  height: 10%;
  padding: 01rem;
  display: flex;
  justify-content: space-between;
  background: rgba(255, 255, 255, 0.393);
  align-items: center;
  border-top-left-radius: 10px;
}
.sidebar-body {
  height: 100%;
  background: rgba(255, 255, 255, 0.663);
  border-bottom-left-radius: 10px;
}
.chatpanel {
  flex: 2;
  display: flex;
  flex-direction: column;
}
.chatpanel-top {
  height: 10%;
  padding: 0 1rem;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: rgba(255, 255, 255, 0.85);
  border-top-right-radius: 10px;
}
.chatpanel-body {
  height: 86%;
  background-image: url(https://img.freepik.com/free-vector/vintage-ornamental-flowers-background_52683-28040.jpg?w=1380&t=st=1671720018~exp=1671720618~hmac=005e3d1a18739b89a19f8e19306f2712e69c07e1565cfa0fa40d5480d6d2ed59);
  overflow: scroll;
}
.chatpanel-bottom {
  height: 10%;
  padding: 0 1rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  background: white;
  border-bottom-right-radius: 10px;
}

::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track{
  background: rgba(255, 255, 255, 0);
}
::-webkit-scrollbar-thumb{
  background: rgb(255, 255, 255);
}
::-webkit-scrollbar-thumb:hover {
  background: rgb(23, 23, 23); 
  transition: .3s;
}