@import url("https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Lobster&family=Nunito+Sans:wght@300;400;800&display=swap");

:root {
  --background-main: #7286d3;
  --background-sub: #8ea7e9;
  --nav-background: #b1b2ff;
  --tect-main: #0a2647;
  --text-sub: #144272;
  --background-button-main: #b1afff;
  --background-button-sub: #bccef8;
}

/* ======= Navbar styling start ======= */
.web__navbar {
  padding: 2rem 4rem;
  display: flex;
  width: 100%;
  height: 50px;
  position: fixed;
  justify-content: space-between;
  align-items: center;
  background: var(--nav-background);
  z-index: 20;
}
.web__navbar-logo a {
  text-decoration: none;
  color: var(--tect-main);
  font-family: "DM Serif Display", serif;
  font-weight: bolder;
  position: relative;
  top: 10px;
}
.web__navbar-links {
  display: flex;
  gap: 50px;
  word-spacing: 2px;
  font-family: var(--body-font);
  display: flex;
  align-items: center;
}
.web__navbar-links a {
  text-decoration: none;
  color: var(--tect-main);
  transition: 0.3s;
  transition: 0.3s;
}
.web__navbar-links a:hover {
  border-bottom: 1px solid black;
}
.web__navbar-links button {
  width: 80px;
  height: 30px;
  border: none;
  border-radius: 5px;
  transition: 0.3s;
  background: var(--background-button-main);
}
.web__navbar-links button:hover {
  background: var(--background-button-sub);
  color: var(--text-sub);
}
.nav__responsive {
  margin-left: 1rem;
  display: none;
  position: relative;
  transition: 0.3s;
}
.nav__responsive svg {
  cursor: pointer;
}
.web__navbar-menu_container {
  display: flex;
  align-items: center;
  flex-direction: column;
  background: var(--background-sub);
  padding: 2rem 0;
  text-align: start;
  position: absolute;
  top: 20px;
  border-radius: 10px;
  right: 20px;
  width: 200px;
  height: 250px;
  box-shadow: 0 0 5 rgba(0, 0, 0, 0.2);
  line-height: 30px;
}
.web__navbar-links__container a {
  padding: 0;
  text-decoration: none;
  font-size: 20px;
  color: var(--tect-main);
  transition: 0.3s;
}
.nav__login {
  position: relative;
  left: 10px;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.nav__login button {
  width: 100px;
  height: 40px;
  border: none;
  border-radius: 5px;
  transition: 0.3s;
  border: 1px solid white;
  background: var(--background-button-main);
  color: var(--tect-main);
  position: relative;
  left: -15px;
  top: 10px;
  transition: 0.3s;
}
.nav__login button:hover {
  background: var(--background-button-sub);
  color: var(--text-sub);
}
.web__navbar-links button:hover {
  border: 1px solid white;
  background: var(--background-button-sub);
  color: var(--text-sub);
}
.user-icon {
  background: var(--background-button-sub);
  border: 10px black solid;
  border-radius: 50%;
  color: var(--tect-main);
  font-size: 20px;
  font-weight: 500;
  position: relative;
  top: 10px;
}
.user {
  color: var(--tect-main);
  background: var(--background-sub);
  border-radius: 50%;
  font-size: 40px;
}
/* ======= NavBar Styling End ======= */

/* ======= Header Styling Start ======= */
.web_header {
  padding: 10rem 2rem 4rem 4rem;
}
.header-content {
  height: 600px;
  padding: 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  gap: 20px;
  background: rgba(255, 255, 255, 0.2);
  border-radius: 16px;
  box-shadow: 0 4px 30px rgba(0, 0, 0, 0.1);
  backdrop-filter: blur(0px);
  -webkit-backdrop-filter: blur(0px);
  border: 1px solid rgba(255, 255, 255, 0.3);
}
.header__img img {
  width: 250px;
}
.header-social button {
  width: 130px;
  height: 40px;
  border: none;
  border-radius: 5px;
  text-align: center;
}
.wa {
  background: #128c7e;
}
.fb {
  background: #4267b2;
}

/* ======= Header Styling End ======= */

/* ======= Home Styling Start ======= */
.web__home {
  transition: 0.5s;
}
.home__carousel {
  padding: 6rem 4rem;
}
.carousel_img {
  height: 500px;
}

.home__drop {
  padding: 0 4rem;
}
.home__drop button {
  width: 100%;
  height: auto;
  background: var(--background-button-main);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  color: var(--tect-main);
  font-size: 16px;
  font-weight: 500;
}
.drop__card {
  padding: 1rem 2rem;
  border: 2px solid rgb(255, 255, 255);
  
  border-radius: 0 0 5px 5px;
}
.drop__card-content {
  padding: 1rem 1rem;
  gap: 10px;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
}
.drop__content {
  width: 230px;
  height: 60px;
  background: var(--background-sub);
  color: var(--tect-main);
  font-size: 18px;
  font-weight: 500;
  display: flex;
  flex-direction: column;
  justify-content: center;
  text-align: center;
  border: none;
}

.carousel-desc {
  background-color: var(--background-button-sub); /* Fallback color */
  background-color: var(
    --background-button-main
  ); /* Black w/opacity/see-through */
  color: var(--tect-main);
  font-weight: bold;
  border: 1px solid #f1f1f1;
  position: absolute;
  top: 20%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 80%;
  padding: 20px;
  text-align: center;
}

.home__bar {
  padding: 2rem 4rem;
}
.home__bar ul {
  border: 2px solid white;
  border-radius: 5px;
  height: 120px;
  list-style: none;
  display: flex;
  flex-direction: row;
  justify-content: space-evenly;
  align-items: center;
  text-align: center;
}
.home__bar ul > li {
  display: flex;
  flex-direction: column;
  font-size: 18px;
  color: var(--tect-main);
}
.home__bar li > span {
  font-size: 45px;
  cursor: pointer;
}
.home__bar li > a {
  text-decoration: none;
  color: var(--tect-main);
}

.home__artikel {
  height: 800px;
  padding: 2rem 4rem;
  text-align: center;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
}
.artikel-content {
  overflow: scroll;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
.title {
  background: var(--background-sub);
  color: var(--tect-main);
  width: 100%;
  border: none;
  border-radius: 5px 5px 0 5px;
  font-size: 26px;
}
.artikel__content {
  text-align: start;
  padding: 2rem 0;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  gap: 20px;
}
.up-button {
  z-index: 2;
}
.up-button a {
  text-decoration: none;
  color: var(--tect-main);
  background: var(--background-sub);
  border-radius: 50%;
  width: 50px;
  height: 50px;
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  left: 860px;
}
::-webkit-scrollbar {
  width: 10px;
}
::-webkit-scrollbar-track {
  background: var(--background-button-sub);
}
::-webkit-scrollbar-thumb {
  background: var(--background-button-main);
  border-radius: 10px;
}
::-webkit-scrollbar-thumb:hover {
  background: var(--background-button-sub);
  transition: 0.3s;
}
.content__card {
  border: 1px solid white;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  padding: 15px 15px;
  width: 400px;
  height: 500px;
}
.card-img {
  width: 300px;
  height: 200px;
  object-fit: scale-down;
}
.card-img img {
  width: 300px;
  height: 130px;
}
.card-title,
.card-desc {
  border-radius: 5px;
  width: 300px;
  padding: 15px 10px;
}
.card-btn button {
  width: 200px;
  height: 40px;
  border: none;
  border-radius: 10px;
  font-size: 18px;
  font-weight: 500;
  transition: 0.3s;
  background: var(--background-button-main);
  color: var(--tect-main);
}
.card-btn button:hover {
  background: var(--background-button-sub);
  color: var(--text-sub);
}

/* ======= Home Styling End ======= */

/* ======= Footer Styling Start ======= */

.footer {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background: var(--nav-background);
  color: var(--tect-main);
}
.footer p {
  display: flex;
  justify-content: center;
  align-items: center;
  color: var(--text-sub);
}
.footer a {
  background: var(--background-sub);
  text-decoration: none;
  color: var(--text-sub);
  width: 50px;
  height: 50px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* ======= Footer Styling End ======= */

/* ======= Pemerintahan Styling Start ======= */

.desa__pem {
  transition: 0.3s;
  display: flex;
  flex-direction: column;
  height: auto;
  justify-content: center;
  color: var(--tect-main);
  padding: 2rem 4rem;
}

.desa__pem-feature {
  width: 500px;
  padding-bottom: 2rem;
}

.desa__pem-feature {
  margin: 0;
}
.desa__pem-feature {
  max-width: 700px;
}

.bloc-tabs {
  display: flex;
}
.tabs {
  padding: 15px;
  text-align: center;
  width: 50%;
  background: transparent;
  color: var(--tect-main);
  cursor: pointer;
  border-bottom: 1px solid whitesmoke;
  box-sizing: content-box;
  position: relative;
  outline: none;
}
.tabs:not(:last-child) {
  border-right: 1px solid white;
}

.active-tabs {
  border-bottom: 1px solid transparent;
}

.active-tabs::before {
  content: "";
  display: block;
  position: absolute;
  top: -5px;
  left: 50%;
  transform: translateX(-50%);
  width: calc(100% + 2px);
  height: 5px;
}

button {
  border: none;
}
.content-tabs {
  flex-grow: 1;
}
.content {
  background: transparent;
  padding: 20px;
  width: 100%;
  height: 100%;
  display: none;
  line-height: 20px;
  text-align: start;
}
h1 {
  padding: 0px 0 15px 0px;
  line-height: 30px;
}
hr {
  width: 100px;
  height: 1px;
  border: none;
  margin-bottom: 5px;
}
h4 {
  text-align: left;
  color: var(--tect-main);
}
p {
  color: var(--text-sub);
  line-height: 25px;
}
.active-content {
  display: block;
}
li {
  position: relative;
  left: -15px;
  list-style-type: decimal;
}

/* ======= Pemerintahan Styling End ======= */

/* ======= Kontak Styling Start ====== */

.web__kontak {
  transition: 0.3s;
  padding: 2rem 4rem;
}
.kontak-top {
  text-align: center;
}
.aparatur {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: space-between;
  padding: 5rem 0;
}
.person__card {
  border: 2px solid white;
  border-radius: 5px;
  width: 400px;
  padding: 1rem 1rem;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.person__card img {
  width: 330px;
  height: 400px;
  padding-bottom: 1rem;
}
.card-head,
.card-head h1 {
  font-size: 25px;
  padding: 1rem 0;
}
.card-info {
  padding: 1rem 0;
  text-align: center;
}
.card-info button {
  display: flex;
  flex-direction: row;
  align-items: center;
  border: none;
  border-radius: 10px;
  background: var(--background-button-main);
  transition: 0.3s;
}
.card-info button:hover {
  background: var(--background-button-sub);
}
.card-info button > a {
  text-decoration: none;
  color: var(--tect-main);
  font-size: 20px;
  position: relative;
  left: -5px;
}
.card-info button > a > img {
  width: 40px;
  height: 40px;
  position: relative;
  top: 7px;
}
.kontak__surat {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.kontak__form {
  border: 1px solid white;
  width: 900px;
}

.kontak__form-btn {
  padding: 2rem 0;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
}
.kontak__form-btn button {
  width: 100px;
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 10px;
  text-transform: uppercase;
  border-radius: 10px;
  transition: 0.3s;
  background: var(--background-button-main);
  color: var(--text-sub);
}
.kontak__form-btn button:hover {
  background: var(--background-button-sub);
  border: 1px solid white;
  color: var(--text-sub);
}

/* ======= Kontak Styling End ====== */

/* ======= Data Styling Start ======= */

.desa__data {
  transition: 0.3s;
  padding: 2rem 4rem;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 4rem;
  font-size: 15px;
}
.desa__data-dropdown {
  padding: 0;
}
.dropdown {
  position: relative;
  padding: 0.5rem 0;
}

.dropdown button {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 250px;
  height: 40px;
  font-size: 20px;
  font-weight: 500;
  color: var(--text-sub);
  background: var(--background-button-main);
  border: none;
  border-radius: 5px;
  padding: 0 10px;
  transition: 0.3s;
}
.dropdown button:hover {
  border: 1px solid white;
  background: var(--background-button-sub);
}
.dropdown button > span {
  display: flex;
  align-items: flex-end;
}
.dropdown__menu {
  background: var(--background-sub);
  width: 247px;
  border: none;
  border-bottom-left-radius: 5px;
  border-bottom-right-radius: 5px;
  display: flex;
  flex-direction: column;
  padding: 0.5rem 0 0 0;
}
.dropdown__menu li {
  list-style: none;
}
.dropdown__menu li > button {
  background: transparent;
  display: flex;
  justify-content: flex-start;
  transition: 0.3s;
  position: relative;
  left: -15px;
}
.dropdown__menu li > button:hover {
  background: transparent;
  border: none;
}
.menu {
  position: relative;
  right: 14px;
}
.dropdown__menu li > a > span {
  font-size: 20px;
}

.table-content {
  display: none;
}

.active-table {
  display: block;
}
.table-head tr > th {
  color: var(--tect-main);
}

/* ======= Data Styling End ======= */

/* ======= Informasi Styling Start =======*/

.web__informasi {
  transition: 0.3s;
  padding: 2rem 4rem;
}
.informasi-table {
  padding: 0.8rem;
  border: 2px solid #ffffff;
  border-radius: 10px;
}
.table-top {
  padding: 1rem;
  gap: 20px;
  display: flex;
  align-items: center;
  border: none;
  border-radius: 5px;
  background: var(--background-sub);
  color: var(--tect-main);
}
.icon span {
  font-size: 50px;
}
.judul {
  line-height: 20px;
  color: var(--tect-main);
}
.judul h1,
.judul p {
  position: relative;
  top: 10px;
  line-height: 0;
  font-size: 20px;
  color: var(--tect-main);
}
.table-search {
  text-align: end;
  padding: 2rem 0;
}
.info-anggaran {
  padding: 3rem 0;
}
.anggaran-top {
  background: var(--background-sub);
  border-top-left-radius: 10px;
  border-top-right-radius: 10px;
  border-bottom: 1px solid black;
  display: flex;
  flex-direction: column;
  text-align: center;
}
.anggaran-top p {
  line-height: 0;
  color: var(--tect-main);
}
.anggaran-top p > span {
  position: relative;
  top: 5px;
}
.anggaran-data {
  width: 700px;
}
.data-top {
  padding: 1rem;
  background: var(--background-button-main);
  color: var(--tect-main);
  display: flex;
  flex-direction: row;
}
.data-top p,
.data-top h4 {
  font-size: 15px;
  color: var(--tect-main);
}
.data-chart-card {
  border-bottom: 1px solid white;
  padding: 0.5rem 0;
}

.anggaran-chart {
  display: flex;
  flex-direction: row;
  border: 2px solid white;
  border-bottom-left-radius: 10px;
  border-bottom-right-radius: 10px;
}
.middle {
  border-left: 1px solid white;
  border-right: 1px solid white;
}
.uang {
  font-size: 12px;
}
.data-chart {
  padding: 1rem;
}

.table-back {
  background: var(--background-sub);
}
.table-text {
  color: var(--tect-main);
  text-decoration: none;
}
tr > th {
  color: var(--tect-main);
}
tr > td {
  color: var(--tect-main);
}
/* ======= Informasi Styling end ======= */

/* ======= Responsive Start ======= */

@media screen and (max-width: 1000px) {
  .web__navbar-links {
    display: none;
  }
  .nav__responsive {
    display: block;
  }
  .desa__data {
    display: flex;
    flex-direction: column;
    font-size: 11px;
  }
  .desa__data-dropdown button {
    font-size: 15px;
    width: 200px;
    height: 30px;
  }
  .dropdown__menu {
    width: 200px;
  }
  .table-content h1 {
    font-size: 25px;
  }
  .web__sidebar {
    display: block;
  }
  .up-button a {
    position: relative;
    left: 390px;
  }
  .content__card {
    width: 300px;
    height: 360px;
  }
  .card-img img {
    border-radius: 10px;
    width: 200px;
    height: 80px;
    position: relative;
    left: 50px;
  }
  .card-title h5 {
    font-size: 15px;
  }
  .card-desc p {
    font-size: 12px;
  }
  .card-btn button {
    width: 160px;
    font-size: 15px;
  }
}
@media screen and (max-width: 950px) {
  .up-button a {
    position: relative;
    left: 390px;
  }
}

@media screen and (max-width: 550px) {
  .web__navbar-links {
    display: none;
  }
  .nav__responsive {
    display: block;
  }
  .home__bar ul > li {
    font-size: 14px;
  }
  .desa__data {
    display: flex;
    flex-direction: column;
    font-size: 9px;
  }
  .table-content h1 {
    font-size: 25px;
  }
  .up-button a {
    position: relative;
    left: 180px;
  }
}
@media screen and (max-width: 420px) {
  .web__navbar {
    padding: 2rem 0rem;
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
  }
  .up-button a {
    position: relative;
    left: 130px;
  }
  .web__navbar-links {
    display: none;
  }
  .nav__responsive {
    display: block;
  }
  .web_header {
    padding: 10rem 3rem 4rem 4rem;
    display: flex;
    flex-direction: column;
  }
  .header__img img {
    width: 90px;
    position: relative;
    left: -20px;
  }
  .header-contact {
    display: flex;
    flex-direction: column;
  }
  .header-social a > button {
    position: relative;
    left: -20px;
  }
  .carousel_img {
    height: 150px;
  }
  .carousel-desc {
    display: none;
    padding: 0;
    font-size: 11px;
    width: 250px;
  }
  .drop__content {
    width: auto;
    height: auto;
  }
  .drop__content span {
    font-size: 9px;
  }
  .home__bar ul > li > span {
    font-size: 35px;
  }
  .home__bar ul > li > a {
    font-size: 9px;
  }

  .content__card {
    padding: 0;
    width: 300px;
    font-size: 12px;
  }
  .content__card img {
    width: 250px;
  }
  .content__card h5 {
    font-size: 15px;
    padding: 0 2rem;
  }
  .content__card p {
    padding: 0 2rem;
  }
  .desa__pem {
    padding: 4rem;
    font-size: 12px;
    width: 350px;
  }
  .desa__pem-feature {
    width: 350px;
  }
  .table-top {
    font-size: 12px;
  }
}
.collapsible .content {
  padding: 6px;
  background-color: #eeeeee;
}
.collapsible .header {
  background-color: #dddddd;
  padding: 6px;
  cursor: pointer;
}

/* ======= Responsive End ======= */
