@import url('https://fonts.googleapis.com/css2?family=Dancing+Script:wght@400;500;600&family=Nunito+Sans:wght@300;400;800&display=swap');

:root{
  --background-main: #7286D3;
  --background-sub: #8EA7E9;
  --tect-main: #E5E0FF;
  --text-sub: #FFF2F2;
  --background-button-main: #B1AFFF;
  --background-button-sub: #BCCEF8;
}
[data-theme='light']{
  --backgound: #ffffff;
  --text-primary: #000;
  --text-secondary: #8305c2;
  --accent: #8305c2;
  --border: #333;
  --form-bg: #ffffff;
}

body {
  background-color: var(--background-main);
  color: white;
}