/* ======= Login Page Styling Start ======= */
.web__login {
    height: 100vh;
    transition: 0.3s;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .login {
    transition: 0.3s;
    padding: 10rem 2rem 4rem 4rem;
    text-align: center;
  }
  .container {
    height: 600px;
    width: 500px;
    padding: 4rem 0;
    text-align: start;
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: 0.8rem;
    transition: 0.5s;
    border: 2px solid;
    border-radius: 15px;
  }
  .top {
    font-size: 2rem;
    padding: 2rem 0;
    display: flex;
    justify-content: space-between;
    gap: 50px;
  }
  .top i {
    cursor: pointer;
  }
  .devider {
    font-size: 2rem;
    border: 2px solid;
    background: white;
    height: 5px;
    width: 400px;
  }
  
  form {
    padding: 2rem;
    line-height: 40px;
    display: flex;
    flex-direction: column;
  
    font-size: 1rem;
  }
  form > input {
    width: 400px;
    border: none;
    border-radius: 5px;
  }
  form > button {
    border-radius: 5px;
  }
  form > button:hover {
    border: 1px solid white;
    background: black;
    color: white;
    transition: 0.5s;
  }
  .remember {
    display: flex;
    flex-direction: row;
    align-items: center;
    gap: 10px;
  }
  .remember p {
    position: relative;
    top: 6px;
  }
  .bottom {
    text-align: center;
    line-height: 40px;
    display: flex;
    gap: 80px;
  }
  .bottom a {
    text-decoration: none;
    color: white;
  }
  
  input[type="number"] {
    -webkit-text-security: disc;
    -webkit-user-modify: read-write-plaintext-only;
  }
  .google{
    background: none;
  }
  .google img{
    border-radius: 10px;
  }
  /* ======= Login Page Styling End ======= */
  
