  /* ======= User Styling Start ======= */
  .web__user{
    height: 100vh;
    padding: 10rem 2rem 4rem 4rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .user-img img{
    border-radius: 50%;
    width: 200px;
  }
  .user-card{
    background: rgba(255, 255, 255, 0.649);
    color: black;
    width: 400px;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    border-radius: 10px;
  }
  .profile a > span{
    font-size: 25px;
    color: black;
    text-decoration: none;
  }
  .web__user button{
    background: rgba(255, 255, 255, 0.452);
    width: 200px;
    height: 50px;
    font-size: 25px;
    font-weight: 500;
    border-radius: 30px;
  }
  .user-devider{
    padding-inline-start: 2rem;
    border: 4px solid transparent;
    width: 400px;
    height: 30px;
  }
  
  /* ======= User Styling end ======= */
  
  
  
