/* ======= Sidenav Start ======= */

.sidenav {
  display: flex;
  align-items: center;
  gap: 380px;
}
.sidenav-logo a {
  text-decoration: none;
  color: white;
  font-size: 20px;
  text-transform: uppercase;
}
.sidenav-user img {
  width: 30px;
  background: white;
  border-radius: 50%;
}

/* ======= Sidenav End ======= */

/* ======= Sidebody Start ======= */

.sidebody {
  padding: 0.5rem 0.5rem;
}
.sidebody-search input {
  background: none;
  width: 210px;
  border: none;
  color: white;
  outline: none;
}
.sidebody-search input::placeholder {
  color: white;
}
.sidebody-users {
  display: flex;
  flex-direction: column;
}
/* ======= Sidebody End ======= */

/* ======= Panelnav Start ======= */

.panelnav {
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 790px;
}
.panelnav-other img {
  width: 30px;
  background: white;
  border-radius: 50%;
}
.panelnav-icons {
  display: flex;
  gap: 28px;
}
.panelnav-icons span {
  color: black;
  cursor: pointer;
}

/* ======= Panelnav End ======= */

/* ======= Panelbody Start ======= */

.panelbody-chat {
  display: flex;
  flex-direction: column;
}

.panelchat {
  padding: 1rem;
  display: flex;
  flex-direction: column;
  justify-content: space-between;

  /* overflow: scroll; */
}
.otherschat-card {
  padding: 1rem;
  width: 350px;
  display: flex;
  flex-direction: row;
  align-items: center;
  gap: 30px;
  border: none;
  border-radius: 10px;
}
.card-icons img {
  width: 40px;
}
.card-chats {
  padding: 10px;
  background: rgba(196, 195, 195, 0.615);
  width: 300px;
  height: auto;
  border: none;
  border-radius: 10px;
  border-top-left-radius: 0;
  position: relative;
  top: 30px;
  word-wrap: initial;
}
.card-chats p {
  position: relative;
  color: black;
}
.userchat{
  position: relative;
  top: -120px;
  display: flex;
  flex-direction: row-reverse;
}
.userchat-card {
  padding: 1rem;
  width: 350px;
  display: flex;
  flex-direction: row;
  justify-content: end;
  align-items: center;
  gap: 20px;
  border: none;
  border-radius: 10px;
  position: relative;
  top: 120px;
}
.usercard-chats {
  padding: 10px;
  background: rgba(177, 174, 174, 0.653);
  width: 300px;
  text-align: end;
  height: auto;
  border: none;
  border-radius: 10px;
  border-top-right-radius: 0;
  position: relative;
  top: 30px;
  word-wrap: break-word;
  writing-mode: sideways-lr;
}
.usercard-chats p {
  position: relative;
  color: black;
}

/* ======= Panelbody End ======= */

/* ======= Panelbottom Start ======= */

.panelbottom {
  display: flex;
  justify-content: center;
  align-items: center;
  gap: 20px;
}
.panelbottom input {
  width: 780px;
  height: 60px;
  border: none;
  color: black;
  outline: none;
}
.panelbottom input::placeholder {
  color: black;
}
.panelbottom-icons {
  display: flex;
  align-items: center;
  gap: 20px;
}
.panelbottom-icons button {
  background: rgb(86, 137, 86);
  width: 70px;
  height: 40px;
  border-radius: 10px;
  color: white;
  transition: 0.3s;
}
.panelbottom-icons button:hover {
  background: rgb(65, 105, 65);
}
.panelbottom-icons span {
  color: rgba(12, 12, 12, 0.49);
  cursor: pointer;
}
/* ======= Panelbottom End ======= */

/* ======= Userschat Start ======= */

.userschat {
  padding: 2rem;
}
.userschat button {
  display: flex;
  width: 400px;
  height: 80px;
  background: #ffffffa8;
  border-radius: 10px;
  padding: 1rem;
  gap: 10px;
  transition: 0.3s;
}
.userschat button:hover {
  background: #ffffff69;
}
.users-icon img {
  width: 50px;
}
.users-name p {
  line-height: 5px;
  font-size: 20px;
  font-weight: bold;
  color: black;
}
.users-chat p {
  color: black;
}
/* ======= Userschat End ======= */
